import React, { useEffect } from 'react';
import { setDoc, getFirestore, doc } from "firebase/firestore";
import axios from "axios";
import AddButton from './AddButton';
import Widget from './Widget';
import './App.css';

const db = getFirestore();

export default function Home(props) {
  const aRea = () => {
    props.area.map(item => {
      if (item.isActive === true) {
        axios.get("/" + item.action + "/" + item.reaction)
    		.catch(err =>	{
          console.log(err);
        });
      }
      return "";
    });
  }

  const setAreaDB = async () => {
    setInterval(aRea, 30000);
    try {
      await setDoc(doc(db, "Users", props.info.uid), {area: props.area});
    } catch {
      ;
    }
  }

  useEffect(() => {
    if (props.area !== [])
      setAreaDB();
  }, [props.area]);

  return (
    <div className="home">
      <div className="addButton">
        <AddButton area={props.area} setArea={props.setArea} sign={props.sign}/>
      </div>
      <div className="area">
        {props.area.map((item, index) => {
          return (
            <Widget area={props.area} item={item} setArea={props.setArea} key={index}/>
          )
        })}
      </div>
    </div>
  );
}
