import * as React from 'react';
import { GoogleLogout } from 'react-google-login';
import Button from '@mui/material/Button';
import Avatar from '@mui/material/Avatar';
import axios from "axios";
import { getAuth, signOut } from "firebase/auth";
import './App.css';

const auth = getAuth();

export default function Profile(props) {
  const logOut = async () => {
    alert(`Logout successfully`);
    props.setArea([]);
    props.setSigns("in");
    props.setInfo({});
  };

  const mail = () => {
    axios.get("/Email/" + props.info.email)
    .then(res => {})
    .catch(err =>	{
      console.log(err);
    });
  };

  const out = () => {
    if (props.info.imageUrl === "") {
      return (
        <Button
          variant="contained"
          sx={{ mt: 3}}
          onClick={() => {
            signOut(auth).then(() => {
              alert(`Logout successfully`);
              props.setArea([]);
              props.setSigns("in");
              props.setInfo({});
            })
          }}
        >Logout
        </Button>
      )
    } else {
      return (
        <GoogleLogout
          clientId="427354900946-g2rrmevo58lr0l6p94jh2srr8s14dut4.apps.googleusercontent.com"
          buttonText="Logout"
          onLogoutSuccess={logOut}
        >
        </GoogleLogout>
      )
    }
  };

  mail();
  return (
    <div>
      <div className="detailsProfile">
        <h4 className="yProfile">YOUR PROFILE</h4>
        <h5>My Details</h5>
      </div>
      <div className="profile">
        <Avatar
          src={props.info.imageUrl}
          sx={{ width: 100, height: 100 }}
        />
        <div>
          <p>{props.info.name}</p>
          <p>{props.info.email}</p>
        </div>
      </div>
      <div className="profile">
      </div>
      {out()}
    </div>
  );
}
